import './globals.css';
import { FontProvider } from '@/components/font-provider';

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body>
        <FontProvider>
          {children}
        </FontProvider>
      </body>
    </html>
  );
}
